import React, { useState, useEffect } from 'react';
import { Button } from './modules/Button';
import { Link } from 'react-router-dom';
import './Navbar.css';

function Navbar() {
  const [click, setClick] = useState(false);
  const [button, setButton] = useState(true);

  const handleClick = () => setClick(!click);
  const closePhoneMenu = () => setClick(false);

  const showButton = () => {
    if (window.innerWidth <= 960) {
      setButton(false);
    } else {
      setButton(true);
    }
  };

  useEffect(() => {
    showButton()
  }, []);

  window.addEventListener('resize', showButton);

  return (
    <>
      <nav className='navbar'>
        <div className='navbar-container'>
          <Link to='/' className='navbar-logo' onClick={closePhoneMenu}>

            
            B <i class="fa-solid fa-notes-medical"></i> HEALTH

          </Link>
          <div className='menu-icon' onClick={handleClick}>
            <i className={click ? 'fas fa-times' : 'fas fa-bars'} />
          </div>
          <ul className={click ? 'nav-menu active' : 'nav-menu'}>
            <li className='nav-item'>
              <Link to='/' className='nav-links' onClick={closePhoneMenu}>
                Home
              </Link>
            </li>
            <li className='nav-item'>
              <Link
                to='/services'
                className='nav-links'
                onClick={closePhoneMenu}
              >
                
                Services

              </Link>
            </li>
            <li className='nav-item'>
              <Link
                to='/doctors'
                className='nav-links'
                onClick={closePhoneMenu}
              >
                Doctors
              </Link>
            </li>

            <li>
              <a
                href='http://bloodloof.herokuapp.com/'
                className='nav-links-mobile'
                onClick={closePhoneMenu}
                rel="noreferrer" target="_blank"
              >
              </a>
            </li>
          </ul>
          {button && <Button buttonStyle='btn--outline'>DONATE BLOOD</Button>}
        </div>
      </nav>
    </>
  );
}

export default Navbar;
